@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
}

code {
  font-family: "Poppins", sans-serif;
}

a {
  color: #eee;
}
a:hover {
  color: #fff;
}

.homecard {
  background-color: rgba(0, 16, 32, 0.7) !important;
  color: #fff !important;
  height: 100%;
  min-width: 350px;
  width: "100%";
}

h2 {
  -webkit-margin-before: 0%;
          margin-block-start: 0%;
  -webkit-margin-after: 0%;
          margin-block-end: 0%;
}

.MuiCard-root {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.apexcharts-tooltip-marker {
  background-color: #005bcf !important;
}

#root {
  min-height: 90vh;
}
footer {
  text-align: center;
  background-color: rgba(0, 16, 32, 0.7);
  padding: 15px 0 20px 0;
  /* bottom: 0; */
  width: 100%;
  /* position: absolute; */
}

footer p {
  font-size: 14px;
  margin-top: 15px;
  letter-spacing: 0.5px;
  margin-bottom: 0;
}

footer a {
  color: #fff;
  -webkit-text-decoration: #fff;
          text-decoration: #fff;
}

footer a:hover {
  color: #00b5ff;
  -webkit-text-decoration: #00b5ff;
          text-decoration: #00b5ff;
}

.p-3 {
  padding: 1rem !important;
}

.custom-link:hover {
  color: #00b5ff;
  -webkit-text-decoration: #00b5ff;
          text-decoration: #00b5ff;
}

sup {
  font-size: xx-small;
  vertical-align: top;
  top: 1em;
}

.MuiSvgIcon-root {
  color: white !important;
}

.MuiSelect-select {
  padding: 6px 30px 6px 10px !important;
}

.ads {
  text-align: center;
  background-color: #012;
}

.MuiFab-root {
  background-color: #005bcf !important;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  transition: ease-in;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#web3-login-root .web3login-modal {
  background: orange;
  }
